            shortTitle: "Հիվանդություն, որի ժամանակ լյարդը չի կարողանում նորմալ գործել երկարատև վնասման պատճառով",
            image: liverLogo,
            about: "cirrhosis_about",
            symptoms: "Սկզբնական շրջանում սովորաբար ախտանշաններ չեն լինում։ Հիվանդության խորացման հետ անձը կարող է զգալ հոգնածություն, թուլություն, քոր, ստորին վերջույթների այտուց, մաշկի դեղնություն, որովայնում հեղուկի կուտակում, մաշկը դառնում է հեշտ վնասվող և կարող է պատվել սարդանման անոթային պատկերներով։ Որովայնում կուտակվող հեղուկը կարող է ինքնուրույն վարակվել։ Այլ լուրջ բարդություններից են լյարդային էնցեֆալոպաթիան, կերակրափողի լայնացած երակներից արյունահոսությունը կամ լյարդի քաղցկեղը։ Լյարդային էնցեֆալոպաթիան կարող է առաջացնել շփոթվածություն և գիտակցության կորուստ։",
            diagnosis: "Ցիռոզի ամենահաճախ հանդիպող պատճառներն են ալկոհոլը, հեպատիտ Բ- ն և Ց- ն, լյարդի ոչ ալկոհոլային ծագման ճարպային հիվանդությունները։ Սովորաբար ցիռոզի զարգացման համար անհրաժեշտ է 2 կամ 3 բաժակ ալկոհոլային ըմպելիքների ամենօրյա կիրառում մի քանի տարիների ընթացքում։ Ոչ ալկոհոլային ծագման լյարդի ճարպային հիվանդությունը ունի մի քանի պատճառներ․ քաշի ավելցուկ, դիաբետ, արյան մեջ ճարպաթթուների բարձրացում, գերճնշում։ Ցիռոզի ավելի քիչ հանդիպող պատճառներից են աուտոիմուն հեպատիտը, առաջնային բիլիար խոլանգիտը, հեմոքրոմատոզը, որոշ դեղամիջոցներ և լեղաքարային հիվանդությունը։ Ախտորոշումը հիմնված է արյան հետազոտության, պատկերային հետազոտությունների և լյարդի կենսազննման (բիոպսիայի) վրա։",
            treatment: "Ցիռոզի առաջացման պատճառը բուժելը կօգնի, որպեսզի հիվանդության ընթացքը դանդաղի, ինչպես նաև կնպաստի լյարդի քաղցկեղի առաջացման վտանգի նվազեցմանը: ԻՌ միջամտություններն օգնում են բարդություններից խուսափել: Լյարդի ցիռոզի վերջնական բուժումը լյարդի փոխպատվաստումն է:"
