            image: astmaLogo,
            about: "Բրոնխիալ ասթման թոքերի քրոնիկ հիվանդություն է, որի ժամանակ առաջանում է բրոնխների խցանում։ Սա իր հերթին առաջացնում է այնպիսի ախտանիշների զարգացում, ինչպիսիք են հազը, շնչահեղձությունը, կրծքավանդակի սեղմումը և շնչահեղձությունը:",
            symptoms: "Հիվանդության ամենաբնորոշ դրսեւորումը շնչահեղձությունն է։ Այն ավելի հաճախ առաջանում է ալերգենի հետ շփվելուց հետո կամ սադրիչ գործոնի ազդեցության տակ՝ սառը, խոնավ օդը, ծխախոտի ծուխը ներշնչելիս կամ հոգե-հուզական սթրեսի ժամանակ։ Նոպան զարգանում է հանկարծակի, դրա ախտանիշները արագորեն աճում են: Մարդը զգում է, որ դժվար է արտաշնչել, առաջանում է սաստիկ հազ, առաջանում է շնչահեղձություն, դրսից սուլոց է լսվում։",
            diagnosis: "Վաղ շրջանում ցիռոզն ախտանշաններ չի առաջացնում: Նման դեպքերում ցիռոզի հայտնաբերումը տեղի է ունենում այլ հիվանդությունների հետազոտության ժամանակ: Ցիռոզի ախտորոշման լավագույն մեթոդը լյարդի բիոպսիան է:",
