            function: "Հակասթրեսային և հակաքաղցկեղային դեղամիջոց. Ակտիվացնում է նյութափոխանակությունը, նվազեցնում է լիպիդների և արյան շաքարի մակարդակը, մեծացնում է սպիտակուցի սինթեզի արդյունավետությունը, արագացնում է լյարդի բջիջների վերածնում, կանխում է աթերոսկլերոզի և պեպտիկ խոցի զարգացումը. կանխում է քաղցկեղի պրոցեսի զարգացումը, նպաստում է քաղցկեղով արդեն տուժած բջիջների վերականգնմանը։ Դադարեցնում է թոքերի, արգանդի վզիկի, լյարդի և մելանոմայի քաղցկեղի զարգացումը. բարձրացնում է անձեռնմխելիությունը, օգնում է հագեցնել բջիջները թթվածնով; մեծացնում է սեռական օրգանների ֆունկցիան, տոնուսավորում է երիկամները, բարելավում է միկրոշրջանառությունը, կանխում է արյան մակարդումը, հորմոնացնում է հոգեկան վիճակը, նորմալացնում է քունը, մեծացնում տոկունությունը, դիմադրությունը սթրեսին և օդերևութաբանական պայմանների փոփոխություններին. նպաստում է օրգանիզմից քաղցկեղի թունավորման արտադրանքի, մանրէաբանական և վիրուսային տոքսինների, դեղամիջոցների, ինչպես նաև այլ աղտոտիչների հեռացմանը, ներառյալ. ռադիոնուկլիդներ, ունի բակտերիալ ազդեցություն, ներառյալ բերանի խոռոչում, պաշտպանում և ամրացնում է ատամները. բարելավում է թոքերի օդափոխությունը, պաշտպանում է ռադիոակտիվ ճառագայթումից և ուլտրամանուշակագույն ճառագայթումից:",
