import styled from "styled-components";

export const StyledContainer = styled.div`
    margin-bottom: 30px;
`

export const StyledTitle = styled.h2`
    margin-left: 20px;
    margin-bottom: 20px;
    padding-top: 20px;
    @media screen and (max-width: 500px) {
        margin-left: 0;
        font-size: 22px;
        text-align: center;
    }
`