import styled from "styled-components";

export const StyledTitle = styled.h2`
    margin-top: 20px;
    margin-bottom: 20px;
`

export const StyledLi = styled.li`
    margin-left: 40px;
    font-size: 22px;
    margin-bottom: 10px;
    font-weight: 700;
    @media screen and (max-width: 500px) {
        font-size: 20px;
    }
`